import { Component, OnInit, ViewChild, ElementRef, HostListener, ViewChildren } from '@angular/core';
import { JsonCreate } from 'src/app/services/jsonCreate.service';
import * as moment from 'moment'
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import * as pluginDataLabels from 'chartjs-plugin-datalabels';
import { Label } from 'ng2-charts';






@Component({
  selector: 'app-profile-store',
  templateUrl: './profile-store.component.html',
  styleUrls: ['./profile-store.component.sass']
})
export class ProfileStoreComponent implements OnInit {
  jsonData
  nullTasks = true


  public barChartOptions: ChartOptions = {
    responsive: false,
    // We use these empty structures as placeholders for dynamic theming.
    scales: { xAxes: [{}], yAxes: [{}] },
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };
  public barChartLabels: Label[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartPlugins = [pluginDataLabels];

  public barChartData: ChartDataSets[] = [
    { data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A' },
    { data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B' }
  ];
    
  constructor(private js: JsonCreate) {
    
    // this.value.width = this.spinner.width
    this.js.getSelectedDataFirestore('check-list','date',moment().format('DD-MM-YYYY')).subscribe(value => {
      if (value[0].aliases) {   
      }
      this.jsonData = value[0].aliases
      if (!this.jsonData) {
        console.log(this.jsonData)
        this.nullTasks=false
      }else{
        this.nullTasks=true
      }
    })

    
    

  
    
   }

   width 
  

  ngOnInit() {
    
    this.width = window.innerWidth;
  }

  ngAfterViewInit(){
    console.log(this.spinner)

  }
  
  @ViewChild("shoes",{static: false}) 
  shoes: {
    options,
    selectedOptions,
  }
  @ViewChild("value",{static: false}) value
  step: number
  @ViewChild("spinner",{static: false}) spinner
  



  
  
 

  
  click = 0

  clickBtn() {
    console.log(this.value)
  
    
    console.log(this.shoes.options.length)
    this.step= 100/this.shoes.options.length
    this.click = Math.floor(this.step*this.shoes.selectedOptions.selected.length)
    
    if(this.click===100){
      this.value.nativeElement.style.left ='41px'
    }
    else if(this.click===0) {
      this.value.nativeElement.style.left ='55px'
    }
    else this.value.nativeElement.style.left ='48px'


  }

 
}
